import BaseService from './baseService';
import endpoints from '../constants/endpoints';
import http from '../axios';
import _omit from 'lodash/omit';
import qs from 'query-string';

class RFFlowNetworkStreamCloneService extends BaseService {
  constructor() {
    super(endpoints.rfStreamsClone);
  }

  fetchAll() {
    let endpoint = endpoints.rfStreamsClone();
    return http.get(endpoint);
  }

  fetch = (query = {}) => {
    const flowNetworkId = query.uuid || query.id;
    if (!flowNetworkId) {
      throw new Error('Flow network Id is required for fetching consumers of streams');
    }

    let endpoint = endpoints.rfFlowNetworkClone(flowNetworkId);
    query = _omit(query, ['uuid', 'id']);

    if (query && Object.keys(query).length > 0) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }

    return http.get(endpoint).then(data => {
      return { dataItems: data.stream_clones, extraData: _omit(data, ['stream_clones']) };
    });
  };
}

export const rfFlowNetworkStreamCloneService = new RFFlowNetworkStreamCloneService();
