<template>
  <v-card outlined :key="renderCount">
    <v-card-title> Flow Network Streams Clone </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>

      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <flow-network-streams-clone-grid
            v-if="params.uuid"
            :params="params"
            :getDataCb="getDataCb"
          ></flow-network-streams-clone-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import FlowNetworkStreamsCloneGrid from './flowNetworkStreamsCloneGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import ROUTES from '../../../constants/routes';

export default {
  name: 'flow-network-streams',
  components: {
    FlowNetworkStreamsCloneGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  watch: {
    '$route.params.flowNetworkId': function (val, oldVal) {
      if (val != oldVal) {
        this.renderCount++;
        this.initialize();
      }
    },
  },
  data() {
    return {
      renderCount: 0,
      params: {
        uuid: null,
        with_stream_clones: true,
      },
      breadcrumbItems: [],
      gridData: {},
    };
  },
  mounted() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.params = {
        ...this.params,
        uuid: this.$route.params.flowNetworkId,
      };
      this.setBreadCrumbs();
    },
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
    },
    setBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push({
        text: 'Flow Network Clone',
        align: 'center',
        disabled: false,
        link: ROUTES.rfFlowNetworkClone,
      });
      this.breadcrumbItems.push({ text: this.$route.params.flowNetworkId, align: 'center', disabled: false });
      this.breadcrumbItems.push({ text: 'Streams Clone', align: 'center', disabled: true });
    },
  },
};
</script>
